html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

body ::selection {
  background-color: #222831;
  color: #ffffff;
}

p {
  font-size: 2rem;
  color: #222831;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #393e46;
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.8rem 0.8rem 0.8rem #aaaaaa;
  }
}
.header h1 {
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header h1 a {
  color: #ffffff;
}
.header .contenido-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  margin-bottom: 1rem;
  color: #ffffff;
  font-size: 2.4rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  color: #ffd369;
}

.banner {
  width: auto;
  height: 70rem;
  display: block;
  margin: auto;
  background-size: 100% 100%;
  animation: banner 20s infinite;
  animation-direction: alternate-reverse;
}
@media (min-width: 992px) {
  .banner {
    width: 100%;
  }
}

@keyframes banner {
  0%, 30% {
    background-image: url("/build/img/couple-riding-motorbike-wearing-helmets.webp");
  }
  35%, 65% {
    background-image: url("/build/img/car-engine.webp");
  }
  70%, 100% {
    background-image: url("/build/img/man-driving-car-from-rear-view.webp");
  }
}
.imagen {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
}
.imagen .overlay {
  position: absolute;
  background: white;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.0399510146) 0%, rgba(48, 48, 43, 0.5) 4%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contenido-imagen {
  z-index: 10;
}
.contenido-imagen h2 {
  color: #ffffff;
  font-size: 7rem;
  margin: 0;
}
.contenido-imagen p {
  background-color: #ffd369;
  display: inline-block;
  padding: 1rem 2rem;
  color: #ffffff;
  font-weight: 700;
  font-size: 2rem;
  margin: 0;
}

.sobre-seguros {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre-seguros {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-kc {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido-kc {
    margin-top: 0;
  }
}
.contenido-kc h2 {
  margin-bottom: 2rem;
}
.contenido-kc .fecha {
  color: #ffd369;
  font-weight: 900;
}

.lineUp {
  background-color: #eeeeee;
  padding: 5rem 0;
}
.lineUp h3,
.lineUp p {
  text-align: center;
  font-weight: 700;
}
.lineUp .titulo-servicios {
  color: #222831;
}

@media (min-width: 768px) {
  .servicios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.servicios-contenedor p {
  color: #ffffff;
}

.servicios {
  padding-top: 2rem;
}
.servicios p::after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 10rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.bg-gris {
  background-color: #393e46;
  margin: 2rem;
}

.bg-amarillo {
  background-color: #ffd369;
  margin: 2rem;
}
.bg-amarillo .descripcion-servicio,
.bg-amarillo .nombre-servicio {
  color: #222831;
}

.icono-moto .nombre-servicio::after {
  background-image: url("../../../build/img/incono-moto.png");
}

.icono-auto .nombre-servicio::after {
  background-image: url("../../../build/img/icono-auto.png");
}

.icono-bici .nombre-servicio::after {
  background-image: url("../../../build/img/icono-bici.png");
}

.icono-casa .nombre-servicio::after {
  background-image: url("../../../build/img/icono-casa.png");
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.9);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-cerrar {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #ffd369;
  color: #222831;
  display: grid;
  place-items: center;
  font-weight: 900;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.precios {
  padding: 5rem 0;
}
.precios h3 {
  text-align: center;
  color: #ffffff;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .precios .contenedor-precios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase {
  margin: 3rem 0;
}
.pase-header, .pase-footer {
  padding: 0.5rem;
}
.pase-header p, .pase-footer p {
  text-align: center;
  color: #ffffff;
  margin: 0;
  font-weight: 700;
}
.pase-header {
  background-color: #ffd369;
}
.pase-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pase-body {
  background-color: #ffffff;
}
.pase-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pase-body ul li {
  background-color: #ffffff;
  padding: 1rem;
  border-bottom: 1px solid #393e46;
}
.pase-footer {
  background-color: #ffd369;
}
.pase-footer p {
  font-size: 2.4rem;
  font-weight: 700;
}

.site-footer {
  background-color: #ffd369;
  padding: 2rem;
}
.site-footer p {
  margin: 0;
  text-align: center;
  color: #222831;
  font-weight: 700;
}